import React  from "react";


function Table(props){

    const {pairs} = props;
    const default_cell_css = "text-center px-6 py-2 mr-1 text-neutral-600"
    const colors= {"s": "rgba(101,163,13,0.3)", "p": "rgba(2,132,199,0.3)", "w": "rgba(220,38,38,0.3)"}
    return (
        <div className="w-full block text-center">
            <table className="table-auto  m-auto text-xs">
                <thead>
                <tr className="border-b">
                    <th className={default_cell_css}>Source</th>
                    <th className={default_cell_css}>Translation</th>
                </tr>
                </thead>
                <tbody>
                {pairs.map((x, i)=>{
                    return(
                            <tr key={"tr_id_"+i} className="border-b text-white" style={{backgroundColor: colors[x.cls]}}>
                                <td className={default_cell_css} >{x.src}</td>
                                <td className={default_cell_css}>{x.tgt}</td>
                            </tr>)
                })}
                </tbody>
            </table>
        </div>
    )
}

export default Table;